/* unplugin-vue-components disabled */import http from '@/utils/request';
import { ElLoading } from 'element-plus';
export default {
  data() {
    return {
      details: {},
      participants: [],
      bulletin: [],
      urlWithTimestamp: []
    };
  },
  methods: {
    previewPdf() {
      if (this.details.bulletin) {
        const timestamp = new Date().getTime();
        const urlWithTimestamp = `${this.details.bulletin}?t=${timestamp}`;
        console.log(urlWithTimestamp);
        window.open(urlWithTimestamp);
      } else {
        ElMessage({
          message: "No PDF available.",
          type: "warning"
        });
      }
    },
    async requestJoin(rallyid) {
      let res = await http.post('/api/rally/requestJoin', {
        userid: this.$store.state.userInfo.id,
        username: this.$store.state.userInfo.username,
        rallyid
      });
      let {
        msg,
        status
      } = res.data;
      if (status === 1) {
        ElMessage({
          message: msg,
          type: "error"
        });
      } else {
        ElMessage({
          message: msg,
          type: "success"
        });
      }
    }
  },
  async created() {
    let {
      id
    } = this.$route.query;
    const loadingInstance = ElLoading.service({
      lock: true,
      text: "Loading",
      background: "rgba(0, 0, 0, 0.7)"
    });
    try {
      const [participantsRes, detailsRes] = await Promise.all([http.get(`/api/rally/queryParticipants?rallyid=${id}`), http.get(`/api/rally/queryInfo?id=${id}`)]);

      // Handle participantsRes
      let {
        data: participantsData,
        status: participantsStatus
      } = participantsRes.data;
      if (participantsStatus === 0) {
        this.participants = participantsData;
      }

      // Handle detailsRes
      let {
        data: detailsData,
        status: detailsStatus
      } = detailsRes.data;
      if (detailsStatus === 0) {
        this.details = detailsData;
      }
    } catch (error) {
      console.error(error);
    } finally {
      loadingInstance.close(); //close draw
    }
  }
};